.menu-item {
  margin: auto;
  display: block;
  font-size: 14px;
  padding: 8px 4px;
}

.list:hover {
  & .menu-item {
    display: block;
  }
}
.list-item {
  position: relative;
}

.list-item:hover {
  & .menu-options {
    display: flex;
    align-items: center;
  }
}

.menu-options {
  display: none;
  position: absolute;
  right: calc(4% - 16px);
  background-color: rgba(100, 100, 100, 0.5);
}

.menu-option {
  font-size: 14px;
  padding: 0px 8px;
}

