.App {
  position: relative;
  text-align: center;
  height: 100%;
  background-color: #282c34;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.center-center {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.space-between-center {
  display: flex;
  flex: 1;
  justify-content: space-between;
  align-items: center;
}

